<template>
  <div>
    <base-alert v-if="userInfo.email && ! userInfo.confirmed" type="warning" dismissible>
      <span class="alert-inner--icon"><i class="fas fa-exclamation"></i></span>
      <span class="alert-inner--text">
          <strong> Your email address still needs verification.</strong>
          Your account access may be limited until you click the link sent to <strong>{{userInfo.email}}</strong>.
      </span>
      <base-button size="sm" type="secondary" class="ml-3" :loading="resendingEmail" @click.prevent="resendEmailConfirmation">Resend</base-button>
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </base-alert>
    <base-alert v-if="isLiveStream" type="primary" dismissible>
      <span class="alert-inner--icon"><i class="fas fa-exclamation"></i></span>
      <span class="alert-inner--text"><strong> We're Live!</strong> ChinesePod is streaming live! </span><a class="btn-link text-white" href="https://youtube.com/channel/UCRY8eBLd9tPFw5-JY7S7O8Q/live"><i class="fab fa-youtube"></i> Click Here to Watch Now!</a>
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </base-alert>
    <base-alert v-if="showCurrentNotification" type="primary" dismissible>
      <span class="alert-inner--icon"><i class="fas fa-exclamation"></i></span>
      <span class="alert-inner--text"><strong> Update </strong>
        <br/>
        Our academic team has recently conducted a full review of our course and lesson levels.
        In order to maximize your learning experience, we have revised and changed levels for some lessons and reorganized our courses.
        <br/>
        You can find out more detailed information about the changes here:</span>
      <a class="btn-link text-white" href="https://forum.chinesepod.com/t/updates-on-the-courses-page/6884"> Read more...</a>
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </base-alert>
    <suggestion-notification v-if="!showCurrentNotification && courses && courses.suggestions" :course-data="courses.courses" @dismiss="scheduleSuggestions(Date.now())"/>
  </div>
</template>

<script>
  import {mapState, mapMutations} from 'vuex'

  import SuggestionNotification from "./Notifications/SuggestionNotification";

  export default {
    name: "NotificationArea",
    components: {SuggestionNotification},
    computed: {
      showFeatureUpdates: () => {return new Date() < new Date('2020-02-11')},
      ...mapState({
        userInfo: 'userInfo',
        showDashboardSwitch: 'showDashboardSwitch',
        isLiveStream: 'isLiveStream',
        showSuggestions: 'showSuggestions'
      })
    },
    data () {
      return {
        courses: {},
        resendingEmail: false
      }
    },
    async mounted() {

      if(new Date(this.showSuggestions) < new Date()) {
        this.courses = await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/get-suggestions')
          .then(({data}) => data)
          .catch((err) => {});
        if (this.courses && !this.courses.suggestions) {
          this.scheduleSuggestions(Date.now())
        }
      }
    },
    methods: {
      ...mapMutations({
        scheduleSuggestions: 'SCHEDULE_SUGGESTIONS'
      }),
      async resendEmailConfirmation () {
        this.resendingEmail = true;
        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/account/profile/resend-confirmation')
          .catch(() => {});
        this.resendingEmail = false;
      },
      showCurrentNotification () {
        return new Date() > new Date('2020-07-16') && new Date() < new Date('2020-07-23')
      }
    }
  }
</script>

<style scoped>
  .golden {
    color: gold;
  }

</style>
